.biography {
    color: #717d97;
    text-align: center;
    width: 55vw;
    display: inline-block;
    font-weight: bold;
    margin-bottom: 1em;
    font-size: 1em;
}

.biography span {
    
    font-size: 0.8em;
    margin: 2em;
    display: block;
    text-align: center;
}

.biography-alt-style {
    display: none;
}

@media screen and (max-width: 430px) {
    .biography {
        width: 85vw;
     }
   
  }

@media screen and (max-width: 415px) {

    .biography {
        width: 85vw;
     }
  }


